* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    color: white;
    background: white;
}

body, input, button, textarea {
    font: 400 18px 'Red Hat Text', sans-serif;
}

nav {
    background-color: #49883E;
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    justify-content: center;
    align-items: center;
}

nav .nav-content {
    display: flex;
    flex: 1 1;
    width: 100%;
    max-width: 75%;
    height: 80px;
    align-items: center;
    justify-content: space-between;
}

nav .nav-content h1 {
    font-size: 3vw;
    font-family: 'Montserrat', sans-serif;
}

nav .nav-content .buttons {
    display: flex;
    width: 100%;
    max-width: -webkit-fit-content;
    max-width: -moz-fit-content;
    max-width: fit-content;
    justify-content: center;
    font-family: 'Red Hat Text', sans-serif;
}

nav .nav-content .buttons p {
    color: white;
    font-weight: 500;
    padding: 1vh 2vw;
    border: 1px solid #f0f0f5;
    border-radius: 16px;
    font-family: 'Red Hat Text', sans-serif;
    transition: 400ms;
}

nav .nav-content .buttons p:hover {
    background: white;
    border: 1px solid #49883E;
    color: #49883E;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #49883E;
    color: white;
    min-width: auto;
    justify-content: center;
    transition: 400ms;
}

.dropdown:hover .dropdown-content {
    color: white;
    display: flex;
    flex-direction: column;
    height: auto;
    transition: 400ms;
}

.dropdown-content p {
    border: none !important;
    width: 100%;
    border-radius: 0;
    transition: 400ms;
}

.selected {
    background-color: #36632e;
    color: black !important;
    cursor: default;
    border: none !important;
}

.selected:hover {
    color: black !important;
    background-color: #36632e !important;
}

.session-title {
    color: #36632e;
    margin-top: 30px;
}

#footer {
    height: 140px;
    width: 100%;
    background-color: #36632e;
    font-size: 12px;
    display: flex;
    flex: 1 1;
    align-items: center;
}

#footer .soc-networks {
    align-items: center;
}

#footer .soc-networks,
#footer .dev,
#footer .info {
    justify-content: space-between;
}

::-webkit-scrollbar {
    display: none;
}
#page-landing {
    width: 100vw;
    height: 100vh;
    background:  linear-gradient(
        rgba(0, 0, 0, 0.6), 
        rgba(0, 0, 0, 0.6)
      ), url(/static/media/bg.73dce167.jpg) no-repeat fixed;
    background-size: cover;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#page-landing nav {
    display: flex;
    width: 100%;

    height: 80px;
    background-color: rgba(0,0,0,0.3);

    margin: 0 auto;

    position: absolute;
    top: 0;

    justify-content: center;
    align-items: center;
}

#page-landing nav .nav-content {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 1100px;
}

#page-landing nav .nav-content h3 {
    font-size: 45px;
    font-family: 'Montserrat', sans-serif;
}

#page-landing nav .nav-content .buttons {
    display: flex;
    width: 100%;
    max-width: 300px;
    height: auto;
    margin: auto 0;
    justify-content: space-between;
    
}

#page-landing nav .nav-content .buttons p {
    color: #f0f0f5;
    font-weight: 500;
    padding: 10px 20px;
    border: 1px solid #f0f0f5;
    border-radius: 16px;
    font-family: 'Red Hat Text', sans-serif;
    transition: 400ms;
}

#page-landing nav .nav-content .buttons p:hover {
    background: white;
    color: #49883E;
    border: 1px solid #49883E;
    transition: 400ms;
}

#page-landing #hero-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    line-height: 100px;
}

#page-landing #hero-content h1 {
    font-family: 'Montserrat', sans-serif;
    font-size: 70px;
}

#page-landing #hero-content h3 {
    display: flex;
    color: white;
    width: 100%;
    max-width: 600px;
    font-size: 50px;
    padding: 10px 200px;
    border: 2px solid white;
    border-radius: 45px;
    transition: 600ms;
}

#page-landing #hero-content h3:hover {
    background: #f0f0f5;
    color: #49883E;
    border: 2px solid #49883E;
    transition: 600ms;
}

#page-landing .emailfield {
    display: none;
}
html {
    height: 100vh;
    width: 100vw;
    margin: 0;
    padding: 0;
}

#main-page {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#main-page #cover {
    margin-top: 80px;
    display: flex;
    width: 100%;
    height: 400px;
    background: linear-gradient(
        rgba(0, 0, 0, 0.6), 
        rgba(0, 0, 0, 0.6)), url(/static/media/bg.73dce167.jpg) 90% no-repeat;
    background-size: cover;
    justify-content: center;
    align-items: center;
}

#main-page #cover .cover-content {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 1100px;
    line-height: 60px;
    text-align: center;
}

#main-page #cover .cover-content h1 {
    font-size: 4vw;
    font-family: 'Montserrat', sans-serif;
    font-weight: 900;
}

#main-page #cover .cover-content h5 {
    font-family: 'Red Hat Display', sans-serif;
    font-weight: 400;
}

#main-page #cover .cover-content p {
    display: flex;
    background-color: #49883E;
    width: auto;
    margin: auto 0;
    color: white;
    font-family: 'Red Hat Display', sans-serif;
    border: 1px solid white;
    border-radius: 16px;
    justify-content: center;
    transition: 400ms;
}

#main-page #cover .cover-content p:hover {
    background-color: #37bf5c;
}

#main-page #categories {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 70vw;
    align-items: center;
    justify-content: center;
    line-height: 80px;
}

#main-page #categories h1 {
    color: #49883E;
    font-family: 'Montserrat', sans-serif;
}

#main-page #categories #category {
    display: flex;
    width: 100%;
    height: 250px;
    background-size: cover;
    justify-content: center;
    align-items: center;
    border-radius: 32px;
    margin-bottom: 40px;
}

#main-page #categories #category p {
    color: white;
    font-size: 30px;
    border: 1px solid white;
    text-align: center;
    padding: 2vh 3vw;
    border-radius: 16px;
    font-family: 'Red Hat Display', sans-serif;
    transition: 400ms;
}

#main-page #categories #category p:hover {
    background-color: rgba(0, 0, 0, 0.3);
}

