#category-page h1 {
    display: block;
    align-items: center;
    text-align: center;
}

#article-container,
#product-container {
    display: flex;
    width: 100%;
    max-width: 1100px;
    margin: 0 auto;
    justify-content: center;
    align-items: center;
}

#product {
    width: 40%;
    margin: 15px;
    height: 380px;
    border: 1px solid #cccccc;
    border-radius: 8px;
    transition: 400ms;
}

#article {
    width: 30%;
    margin: 15px;
    height: auto;
    border: 1px solid #cccccc;
    border-radius: 8px;
    transition: 400ms;
}

#article h4,
#product h4 {
    color: #36632e;
    padding: 12px;
    font-family: 'montserrat', sans-serif;
}

#article img,
#product img {
    object-fit: cover;
    width: 100%;
    height: 50%;
}

#article .art-description,
#product .prod-description {
    color: black;
    padding: 12px;
    font-size: 14px;
}

a {
    text-decoration: none;
}

#product:hover {
    width: 41%;
    height: 390px;
    transition: 400ms;
    background-color: #f0f0f5;
}

#article:hover {
    width: 31%;
    height: 390px;
    transition: 400ms;
    background-color: #f0f0f5;
}

@media (max-width: 768px){
    nav .nav-content .buttons {
        display: none;
        flex-direction: column;
        width: 15vw;
        height: 10vh;
    }

    img {
        width: 100%;
        border-radius: 0;
    }

    h1 {
        font-size: 24px;
    }

    h4 {
        font-size: 2.5vw;
    }

    #article-container,
    #product-container {
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        font-size: 5px;
    }

    #article,
    #product {
        height: auto;
        width: 70%;
    }

}

